<template>
    <div class="main">
      <div class="crumb_box">
        <div class="search_box">
          <el-breadcrumb separator="/">
            <el-breadcrumb-item>首页</el-breadcrumb-item>
            <el-breadcrumb-item>代理人设置</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
      </div>
      <div class="clear"></div>
      <div class="application_item">
        <div class="btn_box">
          <div class="btn_box_1"><el-button type="primary" @click="addAgentFormVisible = true">新建</el-button></div>
          <div class="btn_box_2"><el-button  type="text"  @click="deleteAgent">取消代理</el-button></div>
        </div>
        <div class="clear"></div>
        <div class="table_item">
          <el-table ref="multipleTable" stripe :data="tableAgentsList" tooltip-effect="dark" height="calc(100vh - 245px)" style="width: 100%"
                    @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="userName" label="代理人" width="100" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
            <el-table-column prop="beUserName" label="被代理人" width="100" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
            <el-table-column prop="beginDate" label="开始日期" :formatter="dateFormat" width="130" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
            <el-table-column prop="endDate" label="结束日期" :formatter="dateFormat" width="130" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
            <el-table-column label="代理类型" width="100" :show-overflow-tooltip="tableConfig.overflow">
              <template slot-scope="scope">
                <span v-show="scope.row.proxyType == 1">企业</span>
                <span v-show="scope.row.proxyType == 0">个人</span>
              </template>
            </el-table-column>
            <el-table-column prop="workflowName" label="代理内容"  width="130" :show-overflow-tooltip="tableConfig.overflow"></el-table-column>
            <el-table-column> </el-table-column>
            <el-table-column label="操作" width="200">
              <template slot-scope="scope"><el-button type="text" class="renewbtn"  @click="editProxy(scope.row)">修改</el-button></template>
            </el-table-column>
          </el-table>
        </div>
        <div class="pagination">
          <el-pagination
            :page-sizes="[10, 20, 50, 100]"
            :page-size="pagination.sizes"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pagination.total"
            :current-page='pagination.page'
            @current-change='handleCurrentChange'
            @size-change='handleSizeChange'>
          </el-pagination>
        </div>
      </div>

      <!--新建代理-->
      <el-dialog title="新建代理" :visible.sync="addAgentFormVisible"  width="600px" class="uploadadress">
        <el-form :model="agentAddForm" status-icon label-width="92px" class="demo-ruleForm">
          <el-form-item label="代理人：">
            <el-autocomplete
              class="inline-input"
              v-model="agentAddForm.userName"
              :fetch-suggestions="querySearch"
              placeholder="请输入代理人姓名"
              :trigger-on-focus="false"
              @select="handleAgentFormSelect"
            ></el-autocomplete>
          </el-form-item>
          <el-form-item  label="代理期限：">
            <el-date-picker
              v-model="agentAddForm.beginDate"
              type="date"
              placeholder="选择开始日期" style="width: 217.5px">
            </el-date-picker>&nbsp;&nbsp;&nbsp;-&nbsp;&nbsp;&nbsp;
            <el-date-picker
              v-model="agentAddForm.endDate"
              type="date"
              placeholder="选择结束日期"  style="width: 217.5px">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="代理内容：" >
            <el-autocomplete
              class="inline-input"
              v-model="agentAddForm.workflowcontent"
              :fetch-suggestions="queryWorkflowSearch"
              placeholder="请输入代理内容"
              :trigger-on-focus="false"
              @select="handleWorkflowFormSelect"
            ></el-autocomplete>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="onSubmitAddAgent">确 定</el-button>
          <el-button @click="addAgentFormVisible = false">取 消</el-button>
        </div>
        <div class="clear"></div>
      </el-dialog>

      <!--修改代理-->
      <el-dialog title="修改代理" :visible.sync="editAgentFormVisible"  width="600px" class="uploadadress">
        <el-form :model="agentEditForm" status-icon label-width="92px" class="demo-ruleForm">
          <el-form-item label="代理人：">
            <el-autocomplete
              class="inline-input"
              v-model="agentEditForm.userName"
              :fetch-suggestions="querySearch"
              placeholder="请输入代理人姓名"
              :trigger-on-focus="false"
              @select="edithandleAgentFormSelect"
            ></el-autocomplete>
          </el-form-item>
          <el-form-item  label="代理期限：">
            <el-date-picker
              v-model="agentEditForm.beginDate"
              type="date"
              placeholder="选择开始日期" style="width: 217.5px">
            </el-date-picker>&nbsp;&nbsp;&nbsp;-&nbsp;&nbsp;&nbsp;
            <el-date-picker
              v-model="agentEditForm.endDate"
              type="date"
              placeholder="选择结束日期"  style="width: 217.5px">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="代理内容：" >
            <el-autocomplete
              class="inline-input"
              v-model="agentEditForm.workflowcontent"
              :fetch-suggestions="queryWorkflowSearch"
              placeholder="请输入代理内容"
              :trigger-on-focus="false"
              @select="edithandleWorkflowFormSelect"
            ></el-autocomplete>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="onSubmitEidtProxy">确 定</el-button>
          <el-button @click="editAgentFormVisible = false">取 消</el-button>
        </div>
        <div class="clear"></div>
      </el-dialog>

    </div>
</template>

<script>
  import {addProxy,getProxyList,deleteProxy,updateProxy,getProxyUserList,getWorkflowList} from '@/api/UserApi';
  import moment from 'moment'
    export default {
        name: "AgentSetup",
        data() {
        return {
          tableConfig: {
            loading: false,
            overflow: true,
          },
          restaurants1: [],
          restaurants: [],
          workrestaurants1: [],
          workrestaurants: [],
          multipleSelection: [],
          allTableData: [],
          radio:0,
          totalTableData: [],
          currentPage:4,
          //需要给分页组件传的信息
          pagination: {
            page: 1,
            sizes: 20,
            total: 0,
          },
          isShow:false,
          btnStyle: 'icon-arrowdown',
          editAgentFormVisible:false,
          addAgentFormVisible:false,
          agentAddForm:
            {
              userName:"",//代理人
              beuserName:"",//被代理人
            beginDate:new Date(),//代理开始日期
              endDate:new Date(),//代理结束日期
              workflowId:"",//代理内容id
              userId:"",//代理人id
              beUserId:"",//被代理人id
              workflowcontent:""//代理内容
          },
          agentEditForm:
            {
              userName:"",//代理人
              beuserName:"",//被代理人
              beginDate:new Date(),//代理开始日期
              endDate:new Date(),//代理结束日期
              workflowId:"",//代理内容id
              userId:"",//代理人id
              beUserId:"",//被代理人id
              workflowcontent:""//代理内容
            },
          agentAddForm1:
            {
              beginDate:new Date(),//代理开始日期
              endDate:new Date(),//代理结束日期
              workflowId:"",//代理内容id
              userId:"",//代理人id
              beUserId:"",//被代理人id
            },
          proxyIdForm:{
            proxyIds:[],
          },
          tableAgentsList:[
            // {
            //   proxyId: 3,//代理Id
            //   userId: "",
            //   userName: "",//代理人
            //   beUserId: "",
            //   beUserName: "",//被代理人
            //   beginDate: 1553243000,//代理开始日期
            //   endDate: 1553243000,//代理结束日期
            //   workflowId: 26,
            //   workflowName: "",//代理内容
            //   proxyType: ""//代理类型
            // },
          ],
          timeout:  null,
        }
      },

      created(){
          this.getProxyLists();
      },
      methods:{
        dateFormat: (row, column)=> {
          const date = row[column.property]
          if (date === undefined) {
            return ''
          }
          //这里的格式根据需求修改
          return moment(date).format('YYYY-MM-DD')
        },
        dateFrm: (e) =>{
          // console.log(e);
          return moment(e).format('YYYY-MM-DD');
        },
        //获取代理信息列表
        getProxyLists: function () {
          getProxyList({
            page: this.pagination.page,
            size: this.pagination.sizes,
          }).then(res => {
            if (res.data.code === this.$global.successCode) {
              // console.log("数据请求成功", res);
              this.tableAgentsList = res.data.data.list;
              this.pagination.total = res.data.data.total;
            }
          }, function (error) {
            console.log("请求数据失败", error);
          });
        },
        // 【分页】设置page和sizes
        handleCurrentChange(page) {
          this.pagination.page = page;
          this.getProxyLists();
        },
        handleSizeChange(sizes) {
          this.pagination.sizes = sizes;
          this.getProxyLists();
        },

        //添加代理
        onSubmitAddAgent(){
          this.agentAddForm.endDate = new Date(this.agentAddForm.endDate).getTime();
          this.agentAddForm.beginDate =  new Date(this.agentAddForm.beginDate).getTime();
          const form = this.agentAddForm;
          addProxy(form).then((res) => {
            if (res.data.code === this.$global.successCode) {
              //console.log("添加代理成功",res);
              // 操作成功
              this.$message({
                message: "添加代理成功！",
                type: "success"
              });
              this.getProxyLists();
            }
          }, function (error) {
            console.log("添加代理失败",error);
          });

          this.addAgentFormVisible = false
        },

       //修改代理
        editProxy(corItem){
          this.agentEditForm = [];
          this.editAgentFormVisible = true;
          this.agentEditForm = corItem;
          this.agentEditForm.workflowcontent = corItem.workflowName;
        },
        onSubmitEidtProxy() {
          this.agentEditForm.endDate = new Date(this.agentEditForm.endDate).getTime();
          this.agentEditForm.beginDate =  new Date(this.agentEditForm.beginDate).getTime();
          const form = this.agentEditForm;
          updateProxy(form).then((res) => {
            if (res.data.code === this.$global.successCode) {
              //console.log("修改代理成功",res);
              // 操作成功
              this.$message({
                message: "修改代理成功！",
                type: "success"
              });
              this.getProxyLists();
            }
          }, function (error) {
            console.log("修改代理失败",error);
          });

          this.editAgentFormVisible = false
        },

        //取消代理
        handleSelectionChange(val) {
          this.proxyIdForm.proxyIds = [];
          this.multipleSelection = val;
          this.multipleSelection.forEach((item) => {
            this.proxyIdForm.proxyIds.push(item.proxyId);
          });
        },
        deleteAgent(){
          if(this.multipleSelection.length != 0){
            this.$confirm('是否确定取消该代理？', '提示', {confirmButtonText: '确定', cancelButtonText: '取消', type: 'warning'}).then(() => {
              const form = this.proxyIdForm;
              deleteProxy(form).then((res) => {
                if (res.data.code === this.$global.successCode) {
                  //console.log("取消代理成功",res);
                  // 操作成功
                  this.$message({
                    message: "取消代理成功！",
                    type: "success"
                  });
                  this.getProxyLists();
                }
              }, function (error) {
                console.log("取消代理失败",error);
              });
            }).catch(() => {
            });
          }
          else {
            this.$message({
              message: "请选择需要取消的代理人！",
              type: "warning"
            });
          }

        },

        querySearch(queryString, cb) {
            getProxyUserList({query: queryString}).then((res) => {
              if (res.data.code === this.$global.successCode) {
                //console.log("请求用户数据成功",res);
                this.restaurants = [];
                this.restaurants1 = res.data.data;
                this.restaurants = this.restaurants1.map((item) => {
                    return{
                        value:item.label,
                        usersId:item.value
                      }
                  });
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
              }
            }, function (error) {
              console.log("请求用户数据失败",error);
            });
        },
        createFilter(queryString) {
          return (restaurant) => {
            return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
          };
        },
        handleAgentFormSelect(item) {
          this.agentAddForm.userId = item.usersId;
          this.agentAddForm.userName = item.value;
        },
        edithandleAgentFormSelect(item) {
          this.agentEditForm.userId = item.usersId;
          this.agentEditForm.userName = item.value;
        },

        queryWorkflowSearch(queryString, cb) {
          getWorkflowList({query: queryString}).then((res) => {
            if (res.data.code === this.$global.successCode) {
              //console.log("请求工作流数据成功",res);
              this.workrestaurants = [];
              this.workrestaurants1 = res.data.data;
              this.workrestaurants = this.workrestaurants1.map((item) => {
                return{
                  value:item.label,
                  workflowId:item.value
                }
              })
              // 调用 callback 返回建议列表的数据
              cb(this.workrestaurants);
            }
          }, function (error) {
            console.log("请求工作流数据失败",error);
          });
        },
        handleWorkflowFormSelect(item) {
         // console.log(item.workflowId);
         this.agentAddForm.workflowId = item.workflowId;
         this.agentAddForm.workflowcontent = item.value;
        },
        edithandleWorkflowFormSelect(item) {
          //console.log(item.workflowId);
          this.agentEditForm.workflowId = item.workflowId;
          this.agentEditForm.workflowcontent = item.value;
        },



      },
    }
</script>

<style scoped>

</style>



